@import url("https://fonts.googleapis.com/css?family=Roboto");


.PurchaseProof {
    text-align: center;
}

.PurchaseProof h1{
    font-weight: bold;
    font-size: 80px;
}



.PurchaseProof * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  font-family: "Roboto", sans-serif;
}

body {
 background: url("card/bg.png") no-repeat top center;
  width: 100%;
  height: 100vh;
  background-size: cover;
}

.PurchaseProof .checkout_form {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 380px;
  width: 100%;
  background: rgba(225,225,225,0.35);
  padding: 30px;
  border-radius: 25px;
}

.PurchaseProof .card_number {
  margin-bottom: 25px;
}

.PurchaseProof input[type="text"] {
  padding: 10px;
  border-radius: 3px;
  font-size: 16px;
  border: 2px solid #9e9e9e;
  border: 0;
  text-align: center;
}

.PurchaseProof .card_number .input {
  width: 100%;
  text-align: left;
}


.PurchaseProof .card_grp {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
}


.PurchaseProof .expiry_date,
.PurchaseProof .cvc {
  width: 48%;
  display: flex;
}

.PurchaseProof .expiry_date .expiry_input {
  width: 60px;
  margin-right: 10px;
}

.PurchaseProof .cvc {
  justify-content: flex-end;
  align-items: center;
}

.PurchaseProof .cvc .cvc_input {
  width: 80px;
  margin-right: 10px;
}


.PurchaseProof .cvc_img {
  border: 2px solid #fff;
  width: 25px;
  height: 25px;
  font-size: 18px;
  color: #fff;
  font-weight: bold;
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  position: relative;
}

.PurchaseProof .cvc_img .img {
  position: absolute;
  width: 100px;
  height: 80px;
  padding: 10px 10px 5px;
  border-radius: 3px;
  top: -30px;
  right: 33px;
  background: #064887;
  display: none;
}

.PurchaseProof .cvc_img .img img {
  width: 100%;
  height: 100%;
}

.PurchaseProof .cvc_img:hover .img {
  display: block;
}

.btn {
  width: 100%;
  background: #064887;
  color: #fff;
  padding: 10px;
  border-radius: 3px;
  text-align: center;
  font-size: 18px;
  text-transform: uppercase;
  cursor: pointer;
  letter-spacing: 5px;
}

.PurchaseProof  ::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #064887;
}
.PurchaseProof  ::-moz-placeholder {
  /* Firefox 19+ */
  color: #064887;
}
.PurchaseProof  :-ms-input-placeholder {
  /* IE 10+ */
  color: #064887;
}



@media screen and (max-width: 420px) {
  .card_grp {
    flex-direction: column;
  }
  .expiry_date {
    width: 100%;
    justify-content: space-between;
    margin-bottom: 25px;
  }
  .expiry_date .expiry_input {
    width: 48%;
    margin: 0;
  }
  .cvc {
    width: 100%;
    justify-content: space-between;
  }
  .cvc .cvc_input {
    width: 48%;
  }
  .cvc_img .img {
    right: 26px;
  }
}



