@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family:'Times New Roman', Times, serif;
  height: 100%;
  position: absolute;
  left: 0;
  width: 100%;
  scroll-behavior: auto;
}






.navigation {
  background-color: teal;
  height: 40px;
  font-size: 30px;
  font-style:normal;
  font-family:Georgia, 'Times New Roman', Times, serif;
  color: #000;
  text-align:left;
}

.profile nav {
  background-color:lightgreen;
  color:black;
}

.profile nav li {
  background-color:lightgreen;
  color:black;
}

#wrapper {
	max-width: 960px;
	margin: auto;
}
header {
	padding: 20px 0;
}

nav a {
	text-decoration: none;
}

nav ul {
	margin: 0;
	padding: 0;
}

.profile {
  background: #29c5e6;
}

.profile h1 {
  color: rgba(0,0,0,0.6);
  text-shadow: 0px 15px 5px rgba(0,0,0,0.1), 10px 20px 5px rgba(0,0,0,0.05), -10px 20px 5px rgba(0,0,0,0.05);
}


nav li {
	list-style-position: inside;
	font: 14px 'Oswald', sans-serif;
	padding: 10px;
}

.top-menu li {
	display: inline-block;
	padding: 10px 30px;
	margin: 0;
}

.top-menu li.active {
	background: #29c5e6;
	color: #fff;
}

.top-menu a {
	color: black;
}


a {
  color: #FFF;
}
a:hover {
   color: #00F
}

.login {
  background-color:gold;
}

.registration {
  background-color:darkgray;
  margin-top: 0%;
}

.Profile button {
  width: 100%;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

input,label{
  vertical-align:middle;
  margin: 0;
}

.main-nav-active {
  color: tomato !important;
}

button {
  width: 10%;
  height: 100%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

input {
    vertical-align:middle;
}

label{
    color:#222;
    font-family:corbel,sans-serif;
    font-size: 14px;
}
.Buy select {
    width:200px;
}

img {
    width: 400px;
    float: left;    
    margin: 0 15px 0 0;
}

.Buy {
    height: "100%";
    background-color: blue;
}


.submit {
    height: 100px;
}


.auto-label {
    font-family:Arial, Helvetica, sans-serif;
}


.auto  {
    background: linear-gradient(to bottom, #3333ff 0%, #000066 100%);
}

.color {
    background: linear-gradient(to bottom, #3333ff 0%, #6699ff 100%);
}


.date {
    background: linear-gradient(to bottom, #3333ff 0%, #cc00cc 100%);
}

.price {
    background: linear-gradient(to bottom, #3333ff 0%, #003399 100%);
}

.price input {
    width: 100px;
}


.date input {
    width:100px
}

.Buy label {
    padding-left: 15px;
    color: white;
}

.contents {
    color: black;
    text-underline-position: auto;
    background: linear-gradient(21deg, #dd03e4, #5611ec);
}



.auto-info {
    text-align: left;
    background-color: cornflowerblue;
    height: 493px;
}


.auto-info p {
    font-family: 'Courier New', Courier, monospace;
    font-size: 20px;
    color: black;
    margin: 0px 0px 0px 0px;
    text-shadow: 1px 1px 0 green, -1px -1px 0 green, 2px 2px green, -2px -2px green;
    transition: 1s;
}

.auto-info h1{
    margin-left: 50px;
    text-align: center;
}

.main-info {
    text-shadow: 1px 1px 0 yellow, -1px -1px 0 yellow, 1px 1px yellow, -1px -1px yellow;
}

hr {
    border: 1px solid black;
}
.PurchaseProof {
    text-align: center;
}

.PurchaseProof h1{
    font-weight: bold;
    font-size: 80px;
}



.PurchaseProof * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  font-family: "Roboto", sans-serif;
}

body {
 background: url(/static/media/bg.f84e38cc.png) no-repeat top center;
  width: 100%;
  height: 100vh;
  background-size: cover;
}

.PurchaseProof .checkout_form {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  max-width: 380px;
  width: 100%;
  background: rgba(225,225,225,0.35);
  padding: 30px;
  border-radius: 25px;
}

.PurchaseProof .card_number {
  margin-bottom: 25px;
}

.PurchaseProof input[type="text"] {
  padding: 10px;
  border-radius: 3px;
  font-size: 16px;
  border: 2px solid #9e9e9e;
  border: 0;
  text-align: center;
}

.PurchaseProof .card_number .input {
  width: 100%;
  text-align: left;
}


.PurchaseProof .card_grp {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
}


.PurchaseProof .expiry_date,
.PurchaseProof .cvc {
  width: 48%;
  display: flex;
}

.PurchaseProof .expiry_date .expiry_input {
  width: 60px;
  margin-right: 10px;
}

.PurchaseProof .cvc {
  justify-content: flex-end;
  align-items: center;
}

.PurchaseProof .cvc .cvc_input {
  width: 80px;
  margin-right: 10px;
}


.PurchaseProof .cvc_img {
  border: 2px solid #fff;
  width: 25px;
  height: 25px;
  font-size: 18px;
  color: #fff;
  font-weight: bold;
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  position: relative;
}

.PurchaseProof .cvc_img .img {
  position: absolute;
  width: 100px;
  height: 80px;
  padding: 10px 10px 5px;
  border-radius: 3px;
  top: -30px;
  right: 33px;
  background: #064887;
  display: none;
}

.PurchaseProof .cvc_img .img img {
  width: 100%;
  height: 100%;
}

.PurchaseProof .cvc_img:hover .img {
  display: block;
}

.btn {
  width: 100%;
  background: #064887;
  color: #fff;
  padding: 10px;
  border-radius: 3px;
  text-align: center;
  font-size: 18px;
  text-transform: uppercase;
  cursor: pointer;
  letter-spacing: 5px;
}

.PurchaseProof  ::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #064887;
}
.PurchaseProof  ::-moz-placeholder {
  /* Firefox 19+ */
  color: #064887;
}
.PurchaseProof  :-ms-input-placeholder {
  /* IE 10+ */
  color: #064887;
}



@media screen and (max-width: 420px) {
  .card_grp {
    flex-direction: column;
  }
  .expiry_date {
    width: 100%;
    justify-content: space-between;
    margin-bottom: 25px;
  }
  .expiry_date .expiry_input {
    width: 48%;
    margin: 0;
  }
  .cvc {
    width: 100%;
    justify-content: space-between;
  }
  .cvc .cvc_input {
    width: 48%;
  }
  .cvc_img .img {
    right: 26px;
  }
}



